import React, { useEffect } from 'react'
import AccountBalance from './components/AccountBalance/AccountBalance';
import CoinList from './components/CoinList/CoinList';
import Header from './components/Header/Header';
import styled from 'styled-components';
import axios from 'axios';

const AppRoot = styled.div`
  text-align: center;
`;

const AppBody = styled.div`
  background-color: darkcyan;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 70vh;
  padding: 50px;
`;

const COIN_AMOUNT = 5;
const formatPrice = price => parseFloat(price.toFixed(4));

function App(props) {

  const [balance, setBalance] = React.useState(10000);
  const [showBalance, setShowBalance] = React.useState(false);
  const [coinData, setCoinData] = React.useState([]);

  useEffect( () => {
    if (coinData.length === 0) {
      componentDidMount();
    }
  });

  const componentDidMount = async () => {
    //componentDidMount
    const response = await axios.get('https://api.coinpaprika.com/v1/coins');
    const idList = response.data.slice(0, COIN_AMOUNT).map(coin => coin.id);
    const result = await Promise.all(idList.map(coin => axios.get(`https://api.coinpaprika.com/v1/tickers/${coin}`)));
    const coinData = result.map(coin => (
        {
          key: coin.data.id,
          name: coin.data.name,
          ticker: coin.data.symbol,
          balance: 0,
          price: formatPrice(coin.data.quotes.USD.price)
        }
      ));
    setCoinData(coinData)
  }

  const handleRefresh = async (valueChangeId) => {
    const response = await axios.get(`https://api.coinpaprika.com/v1/tickers/${valueChangeId}`);
    const newCoinData = coinData.map(coin => {
      const newValues = {...coin};

      if (valueChangeId === coin.key) {
        newValues.price = formatPrice(response.data.quotes.USD.price);
        console.log(newValues.price);
      }

      return newValues;
    });

    setCoinData(newCoinData);
  }

  const handleBuy = (ticker, price, id) => {
    const amountToBuy = parseFloat(prompt(`How much ${ticker} would you like to buy?`));

    if (typeof(amountToBuy) !== 'number'
    || amountToBuy == null
    || amountToBuy < 0
    || isNaN(amountToBuy)) {
      alert('You must enter a valid number.')
    } else {
      if (amountToBuy * price > balance) {
        alert('Balance is not enough to buy that amount.')
      } else {
        alert('Transaction Succesful.');
        setBalance(balance - amountToBuy * price);
        const newCoinData = coinData.map(coin => {
          let newValues = {...coin};
          if (id === coin.key) {
            newValues.balance+= amountToBuy;
          }
          return newValues;
        });
        setCoinData(newCoinData);
      }
    }
  }

  const handleSell = (ticker, price, id, coinBalance) => {
    const amountToSell = parseFloat(prompt(`How much ${ticker} would you like to sell?`));
    console.log(amountToSell)
    if (typeof(amountToSell) !== 'number'
    || amountToSell == null
    || amountToSell < 0
    || isNaN(amountToSell)) {
      alert('You must enter a valid number.')
    } else {
      if (amountToSell > coinBalance) {
        alert('Balance is not enough to sell that amount.')
      } else {
        alert('Transaction Succesful.');
        setBalance(balance + amountToSell * price);
        const newCoinData = coinData.map(coin => {
          let newValues = {...coin};
          if (id === coin.key) {
            newValues.balance-= amountToSell;
          }
          return newValues;
        });
        setCoinData(newCoinData);
      }
    }
  }

  const helicopterDrop = () => {
    setBalance(balance + 1200);
  }

  const toggleBalance = () => {
    setShowBalance(oldValue => !oldValue);
  }

  return (
  <AppRoot>
    <Header />
    <AppBody>
      <AccountBalance
        amount={balance}
        toggleBalance={toggleBalance}
        showBalance={showBalance}
        helicopterDrop={helicopterDrop} />
      <CoinList
        coinData={coinData}
        handleRefresh={handleRefresh}
        showBalance={showBalance}
        handleBuy={handleBuy}
        handleSell={handleSell}
        />
    </AppBody>
  </AppRoot>
  );

}

export default App;



