import React from 'react'
import { PropTypes } from 'prop-types';
import styled from 'styled-components';

const Td = styled.td`
    border: 1px solid white;
    width: 120px;
    padding: 0.3rem;
`;

export default function Coin(props) {

    const handleClick = (e) => {
        e.preventDefault();
        props.handleRefresh(props.id);
    }

    const handleClickBuy = (e) => {
        e.preventDefault();
        props.handleBuy(props.ticker, props.price, props.id)
    }

    const handleClickSell = (e) => {
        e.preventDefault();
        props.handleSell(props.ticker, props.price, props.id, props.balance)
    }

    return (
        <tr>
        <Td>{props.name}</Td>
        <Td>{props.ticker}</Td>
        <Td>${props.price}</Td>
        {props.showBalance && <Td>{props.ticker} {props.balance}</Td>}
        <Td>
            <form action="#" method="POST">
                <button onClick={handleClick}>Refresh</button>
                <button onClick={handleClickBuy}>Buy</button>
                <button onClick={handleClickSell}>Sell</button>
            </form>
        </Td>
    </tr>
    );
}

Coin.propTypes = {
    id: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired,
    ticker: PropTypes.string.isRequired,
    price: PropTypes.number.isRequired,
}




